<template>
  <div class="home-container">
    <van-nav-bar class="page-nav-bar"
                 fixed>
      <van-button type="info"
                  slot="title"
                  icon="search"
                  class="search-btn"
                  to="/search">搜索</van-button>
    </van-nav-bar>
    <!-- tab滑块 -->
    <van-tabs v-model="active"
              animated
              swipeable
              swipe-threshold=3
              class="channel-tabs">
      <van-tab :title="item.name"
               v-for="item in channels"
               :key="item.id">
        <art-list :channels=item></art-list>
      </van-tab>
      <div slot="nav-right"
           class="spaceho"></div>
      <div slot="nav-right"
           class="right-btn"
           @click="isShow=true">
        <i class="iconfont icongengduo"></i>
      </div>
    </van-tabs>
    <!-- 频道编辑页 -->
    <van-popup v-model="isShow"
               closeable
               close-icon-position="top-left"
               position="bottom"
               :style="{ height: '100%' }">
      <channel-edit :my-channel=channels
                    :active=active
                    @changeActive='updateActive'></channel-edit>
    </van-popup>
  </div>
</template>

<script>
import { getUserChannels } from '@/api/user'
import artList from './components/art-list'
import channelEdit from './components/channel-edit'
import { mapState } from 'vuex'
import { getItem } from '@/utils/storage'
export default {
  data () {
    return {
      active: 0,
      // 频道列表
      channels: [],
      isShow: false
    }
  },
  components: {
    artList,
    channelEdit
  },
  methods: {
    async loadChannels () {
      // 登录情况
      var channels = []
      if (this.user) {
        const { data: res } = await getUserChannels()
        channels = res.data.channels
      } else {
        // 未登录
        // 本地有频道信息
        const localChannel = getItem('userChannels')
        if (localChannel) {
          channels = localChannel
        } else {
          // 本地没有频道信息,用默认的频道列表
          const { data: res } = await getUserChannels()
          channels = res.data.channels
        }
      }
      this.channels = channels
    },
    updateActive (index, isShow = true) {
      this.active = index
      this.isShow = isShow
    }
  },
  created () {
    this.loadChannels()
  },
  computed: {
    ...mapState(['user'])
  }
}
</script>

<style lang="scss" scoped>
.home-container {
  padding-top: 174px;
  padding-bottom: 100px;
  ::v-deep.van-nav-bar__title {
    max-width: unset;
  }
  .search-btn {
    width: 555px;
    height: 64px;
    border-radius: 32px;
    background-color: #5babfb;
    border: 0;
    font-size: 28px;
    .van-icon {
      font-size: 32px;
    }
  }
  ::v-deep .channel-tabs {
    .van-tabs__wrap {
      height: 82px;
      position: fixed;
      top: 92px;
      z-index: 1;
      left: 0;
      right: 0;
    }
    .van-tab {
      min-width: 200px;
      border-right: 1px solid #edeff3;
      font-size: 30px;
      color: #777;
    }
    .van-tab--active {
      color: #333;
    }
    .van-tabs__line {
      width: 31px;
      height: 6px;
      background: #3296fa;
      border-radius: 3px;
      margin-bottom: 8px;
    }
    .spaceho {
      flex-shrink: 0;
      width: 66px;
      height: 82px;
    }
    .right-btn {
      position: fixed;
      right: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 66px;
      height: 82px;
      background-color: #fff;
      opacity: 0.9;
      .icongengduo {
        font-size: 33px;
      }
      &::before {
        content: "";
        position: absolute;
        left: 0;
        width: 1px;
        height: 100%;
        background: url("~@/assets/gradient-gray-line.png");
        background-size: contain;
      }
    }
  }
}
</style>
