<template>
  <div class="channelEdit">
    <van-cell :border='false'>
      <div slot="title"
           class="title-text">我的频道</div>
      <van-button class="edit-btn"
                  round
                  plain
                  size="mini"
                  @click="edit"
                  type="danger">{{editText}}</van-button>
    </van-cell>
    <!-- 我的频道 -->
    <van-grid :gutter="10">
      <van-grid-item v-for="(value,index) in myChannel"
                     :key="value.id"
                     @click="changeChannel(value)">
        <van-icon slot="icon"
                  v-show="isEdit&&!fiexChanel.includes(value.id)"
                  name="clear" />
        <span slot="text"
              class="text"
              :class="index===active?'redactive':''">{{value.name}}</span>
      </van-grid-item>
    </van-grid>
    <!-- 频道推荐 -->
    <van-cell :border='false'>
      <div slot="title"
           class="title-text">频道推荐</div>
    </van-cell>
    <van-grid :gutter="10"
              class="rem-grid">
      <van-grid-item v-for="value in reChannels"
                     :key="value.id"
                     icon='plus'
                     @click="addChannels(value)"
                     :text="value.name" />
    </van-grid>
  </div>
</template>

<script>
import { getAllChannel, addUserChannel, delUserChannel } from '@/api/channelsList'
import { mapState } from 'vuex'
import { setItem } from '@/utils/storage'

export default {
  props: {
    myChannel: Array,
    active: Number
  },
  data () {
    return {
      allChannelsList: [],
      isEdit: false,
      editText: '编辑',
      fiexChanel: [0]// 不允许被删除的频道，0是推荐频道的id
    }
  },
  computed: {
    // 推荐频道，应该是全部频道-我的频道
    reChannels () {
      // const channels = []
      // this.allChannelsList.forEach(item => {
      //   const res = this.myChannel.find(val => {
      //     return val.id === item.id
      //   })
      //   if (!res) {
      //     channels.push(item)
      //   }
      // })
      // return channels
      return this.allChannelsList.filter(item => {
        return !this.myChannel.find(val => {
          return val.id === item.id
        })
      })
    },
    ...mapState(['user'])
  },
  methods: {
    // 获取全部频道
    async loadUserChannels () {
      try {
        const { data: res } = await getAllChannel()
        // console.log(res)
        this.allChannelsList = res.data.channels
      } catch (err) {
        this.$toast('数据获取失败')
      }
    },
    // 添加频道
    async addChannels (channel) {
      this.myChannel.push(channel)
      // 登录的情况
      if (this.user) {
        try {
          const dataObj = {
            id: channel.id, // 频道id
            seq: this.myChannel.length // 顺序序号，就是用户频道的长度
          }
          await addUserChannel(dataObj)
        } catch (err) {
          this.$toast('添加频道失败，请稍后重试')
        }
      } else {
        // 未登录的情况，把频道信息存到本地
        setItem('userChannels', this.myChannel)
      }
    },
    // 切换编辑和完成状态
    edit () {
      this.isEdit = !this.isEdit
      if (this.isEdit) {
        this.editText = '完成'
      } else {
        this.editText = '编辑'
      }
    },
    // 删除我的频道列表
    changeChannel (value) {
      // console.log(value)
      // 编辑状态下
      if (!this.isEdit) {
        const i = this.myChannel.findIndex(item => {
          return item.id === value.id
        })
        this.$emit('changeActive', i, false)
      } else {
        // 完成状态下

        // 如果是固定的频道，不能被删除
        if (this.fiexChanel.includes(value.id)) {
          return
        }
        const i = this.myChannel.findIndex(item => {
          return item.id === value.id
        })
        if (i <= this.active) {
          this.$emit('changeActive', this.active - 1)
        }
        this.myChannel.splice(i, 1)
        this.delChannel(value.id)
      }
    },
    // 删除
    async delChannel (id) {
      // 登录情况
      if (this.user) {
        try {
          await delUserChannel(id)
        } catch (err) {
          this.$toast('删除频道失败，请稍后重试')
        }
      } else {
        // 未登录
        setItem('userChannels', this.myChannel)
      }
    }
  },
  created () {
    this.loadUserChannels()
  }
}
</script>

<style lang="scss" scoped>
.channelEdit {
  padding: 85px 0;
  border-radius: 10px;
  .edit-btn {
    width: 100px;
    height: 50px;
    font-size: 26px;
    color: #f85959;
    border: 1px solid #f85959;
  }
  .title-text {
    font-size: 32px;
    color: #333;
  }
  ::v-deep.van-grid-item {
    width: 160px;
    height: 86px;
    .van-grid-item__icon-wrapper {
      position: unset;
    }
    .van-icon-clear {
      position: absolute;
      right: -10px;
      top: -10px;
      font-size: 30px;
      color: #cacaca;
      z-index: 5;
    }
    .van-grid-item__content {
      background-color: #f4f5f6;
      white-space: nowrap;
      .van-grid-item__text,
      .text {
        font-size: 28px;
        color: #222;
        margin-top: 0;
      }
      .redactive {
        color: red;
      }
    }
  }
  ::v-deep.rem-grid {
    .van-grid-item__content {
      flex-direction: row;
      .van-icon-plus {
        font-size: 28px;
        margin-right: 6px;
      }
    }
  }
}
</style>
