<template>
  <div class="art-container"
       ref="art-container">
    <van-pull-refresh v-model="refreshing"
                      @refresh="onRefresh"
                      :success-text="successText"
                      success-duration="1000">
      <van-list v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                :error.sync="error"
                error-text="请求失败，点击重新加载"
                @load="onLoad">
        <!-- <van-cell v-for="item in list"
                  :key="item.art_id"
                  :title="item.title" /> -->
        <art-item v-for="(item,index) in list"
                  :key="index"
                  :article='item'></art-item>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import { getArtList } from '@/api/article'
import artItem from '@/components/article-item/artItem'
import { debounce } from 'lodash'
export default {
  name: 'artList',
  props: {
    channels: Object
  },
  data () {
    return {
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      timestamp: null,
      error: false,
      successText: '刷新成功',
      scrollTop: 0 // 记录距离顶部的距离
    }
  },
  methods: {
    async onLoad () {
      // 1 请求获取数据
      try {
        const obj = {
          channel_id: this.channels.id, // 频道的id
          // 请求第一页数据：可以用当前最新时间戳 Date.now()
          // 请求后面的数据，要使用接口返回的pre_timestamp时间戳
          timestamp: this.timestamp || Date.now(), // 简单理解就是页码值
          with_top: 1
        }
        const { data: res } = await getArtList(obj)
        // console.log(res)
        // 2 把请求的数据放到list中
        this.list.push(...res.data.results)
        // 3 数据加载完成后把加载状态设置为结束
        this.loading = false
        // 4 判断数据是否加载完毕
        if (this.list.length) {
          // 更新获取下一页数据的时间戳
          this.timestamp = res.data.pre_timestamp
        } else {
          this.finished = true
        }
      } catch (err) {
        this.error = true
        // 请求失败，关闭loading
        this.loading = false
      }
    },
    // 下拉刷新
    async onRefresh () {
      try {
        const obj = {
          channel_id: this.channels.id, // 频道的id
          // 请求第一页数据：可以用当前最新时间戳 Date.now()
          // 请求后面的数据，要使用接口返回的pre_timestamp时间戳
          timestamp: Date.now(), // 刷新要获取最新数据，要用最新时间戳
          with_top: 1
        }
        const { data: res } = await getArtList(obj)
        // console.log(res)
        // 将最新数据追加到数组顶部
        this.list.unshift(...res.data.results)
        // 关闭下拉刷新的状态
        this.refreshing = false
        this.successText = `刷新成功，更新了${res.data.results.length}条数据`
      } catch (err) {
        this.refreshing = false
        this.successText = '刷新失败'
      }
    }
  },
  components: {
    artItem
  },
  mounted () {
    const artHeight = this.$refs['art-container']
    artHeight.onscroll = debounce(() => {
      this.scrollTop = artHeight.scrollTop
    }, 50)
  },
  // 从缓存状态被激活时触发的钩子函数
  activated () {
    this.$refs['art-container'].scrollTop = this.scrollTop
  }
}
</script>

<style lang="scss" scoped>
.art-container {
  height: 79vh;
  overflow-y: auto;
}
</style>
